import Data from "@/js/core/data";

const Schedulely_Server = "https://api.${region}.500apps.com/schedulely/v1/";
const Manage_Schedulely_Server = "https://api.500apps.com/schedulely/v1/";

class Schedulely {
  // Get Data by Params
  get(params) {
    let url = Schedulely_Server + params;
    let data = new Data(url);
    data.disablePCors();
    return data.get();
  }

  // Add Data
  add(params, form) {
    let url = Schedulely_Server + params;
    let data = new Data(url);
    data.disablePCors();
    return data.multi_call(form);
  }

  // Edit Data
  edit(id, params, form) {
    let url = Schedulely_Server + params;
    let data = new Data(url);
    data.disablePCors();
    return data.edit(id, form);
  }
  get_agents(region, params) {
    let url = "";
    if (region && (region == "dev" || region == "qa")) url = Schedulely_Server + params;
    else url = Manage_Schedulely_Server + params;
    let data = new Data(url);
    data.disablePCors();
    return data.get();
  }
}

export default new Schedulely();
